.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .image {
    &__coffee {
      width: 30rem;
      height: 30rem;
    }
    &__github {
      width: 10rem;
      height: 10rem;
    }
  }

  .information {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__repoboxWrapper {
      display: flex;
    }

    &__clickme {
      position: relative;
      top: 1.5rem;
      left: 0.5rem;
      font-size: 1.2rem;
      line-height: 1.5;
      display: flex;
      align-items: flex-start;

      &__arr {
        width: 1.7rem;
        padding-right: 0.5rem;
      }
    }

    &__github {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    &__name {
      margin-bottom: 0.5rem;
    }

    &__technologies {
      width: 30rem;
      margin-left: 7rem;
      margin-bottom: 3rem;
    }
  }
}
